/* global global, Office, self, window */

Office.initialize = function (reason) {
};

Office.onReady(() => {
  // If needed, Office.js is ready to be called
});


var popupDialog;
var btnEvent;

/**
 * Shows a popup saying "Hello World" when the command is executed.
 * @param event {Office.AddinCommands.Event}
 */
function showPopUp(event) {

  //Saved out of scope, so that the event can be finished after dialog is closed
  btnEvent = event;

  // DialogOptions to specify the Dialog and Url to the Dialog's html 
  var url = new URI('./dialog.html').absoluteTo(window.location).toString();
  var dialogOptions = { height: 40, width: 30, displayInIframe: true };
  var context = Office.context;
  var item = context.mailbox.item;
  if(item){
    localStorage.setItem("sender", JSON.stringify(item.sender));
    localStorage.setItem("subject", item.subject);
    localStorage.setItem("attachments", JSON.stringify(item.attachments));
  }else{
    console.log("item not found");
  }
  // Display the dialog
  Office.context.ui.displayDialogAsync(url, dialogOptions, function(result){
    popupDialog = result.value;
    popupDialog.addEventHandler(Office.EventType.DialogEventReceived, dialogClosed);
  });
}

function dialogClosed(message) {
  popupDialog = null;
  // Be sure to indicate when the add-in command function is complete
  btnEvent.completed();
  btnEvent = null;
  //TO-DO: Cleanup session storage
}

function getGlobal() {
  return typeof self !== "undefined"
    ? self
    : typeof window !== "undefined"
    ? window
    : typeof global !== "undefined"
    ? global
    : undefined;
}

const g = getGlobal();

// The add-in command functions need to be available in global scope
g.showPopUp = showPopUp;
